import { createApp } from 'vue/dist/vue.esm-bundler';
import App from './App.vue'
import router from './router'
import axios from 'axios'
import './assets/sass/style.scss'
// import './assets/sass/timerBtn.scss'
import './assets/sass/style_chk.scss'
import './assets/sass/theme.scss'
import VueGtag from 'vue-gtag-next'
import SlideUpDown from 'vue-slide-up-down'

const popupWindow = {
    props:[],
    template : `
	<div id="overlay" v-show="isShowModal" v-on:click="closeOverlay($event)">
		<div id="content" style="text-align:center;" :class="[modalWidth, popupKbn]">
			<div>
				<div v-show="this.title != ''">
					<p class="font_l">{{ title }}</p>
					<div class="sp_line_xl" />
				</div>
				<div v-show="this.body != ''">
					<div style="text-align:center; white-space: pre-line;" class="font_m">{{ this.body }}</div>
				</div>
				<div v-show="kbn == 'DELETE'" style="width:100%; margin:auto;">
					<span class="font_l font_b">暗証番号を入力してください</span>
					<div class="sp_line_m" />
					<p class="errMsg" v-if="this.errorMessage != ''">▼ {{this.errorMessage}}</p>
					<input type="text" class="form-control inputBase" style="width:150px; margin:auto" v-model="deleteNo" placeholder="" maxlength="10">
						
					<div class="sp_line_m" />

					<button type="button" v-on:click="pushDelete" class="btnBase" style="width:40%;">削除する</button>
				</div>

				<div v-show="kbn == 'REPORT'" style="width:100%; margin:auto;">
					<span class="font_l font_b">通報内容を入力してください</span>
					<div class="sp_line_m" />

					<textarea style="width:80%; margin:auto" class="form-control inputBase" rows="3" v-model="reportMessage" placeholder="" maxlength="200"></textarea>
						
					<div class="sp_line_m" />

					<button type="button" v-on:click="pushReport" class="btnBase" style="width:40%;">通報する</button>
				</div>

				<div v-show="this.kbn == 'CONFIRM'">
					<div class="sp_line_xl" />
					<div style="text-align:center">
						<button type="button" v-on:click="pushConfirmOk" class="btnBase" style="width:40%; margin-right:10px">{{ confirmYesLabel }}</button>
						<button type="button" v-on:click="pushConfirmCancel" class="btnBase" style="width:40%; margin-left:10px">キャンセル</button>
					</div>
				</div>
				
				<div v-show="this.kbn == 'POST_SUCCESS' || this.kbn == 'DELETE_SUCCESS' || this.kbn == 'REPORT_SUCCESS' || this.kbn == 'COPY'">
					<img :src="this.$root.IMG_PATH + nekoMoji" style="width:90%">
					<img :src="this.$root.IMG_PATH + 'icon_neko.png'" style="width:90%; margin-top:-20px">
				</div>

				<div v-show="this.kbn == 'DELETE_SUCCESS' || this.kbn == 'COMPLETE' || this.kbn == 'WARNING_POST' || this.kbn == 'POST_SUCCESS' || this.kbn == 'REPORT_SUCCESS' || this.kbn == 'COPY'">
					<div class="sp_line_xl" />
					<div style="text-align:center">
						<button type="button" v-on:click="pushPopupBtn" class="btnBase" style="width:40%; margin-right:10px">OK</button>
					</div>
				</div>
			</div>
		</div>
	</div>
	`,
	data: function () {
		return {
			kbn : "",
			title : "",
			body : "",
			nickName : "",
			confirmYesLabel : "",
			modalLinkList : [],

			modalScoreList: [],

			isShowModal : false,
			selCategoryIndex : -1,

			deleteNo : "",
			reportMessage : "",

			errorMessage : "",
		}
	},
	computed : {
		popupKbn () {
			if (this.kbn == "COPY" || this.kbn == "POST_SUCCESS" || this.kbn == "DELETE_SUCCESS" || this.kbn == "REPORT_SUCCESS") {
				return "popupB";
			} else {
				return "popupA";
			}
		},
		modalWidth () {
			// if (this.kbn == "COPY") {
			// 	return "modalWidth-B";
			// } else {
				return "modalWidth-A";
			// }
		},
		closeBtnName () {
			if (this.kbn == "UPDATE") {
				return "TOPに戻る";
			}
			return "閉じる";
		},
	},
	methods : {
		pushDelete : function() {
			this.$emit('deleteAction', this.deleteNo);
		},
		pushReport : function() {
			this.$emit('reportAction', this.reportMessage);
		},
		pushPopupBtn : function() {
			if (this.kbn == "COMPLETE" || this.kbn == "WARNING_POST" || this.kbn == "COPY" || this.kbn == "REPORT_SUCCESS") {
				this.closeModal();
			} else {
				this.$emit('popupAction', this.kbn);
			}
		},
		showModal : function(kbnStr) {
			document.getElementById('body').className = "modalBg";
			this.errorMessage = "";
			this.kbn = kbnStr;
			this.isShowModal = true;
		},
		stopModalEvent: function(event) {
			event.stopPropagation()
		},
		pushConfirmCancel : function () {
			this.closeModal();
		},
		closeModal: function() {
			document.getElementById('body').className = "";
			//モーダルを閉じる
			this.kbn = "";
			this.title = "";
			this.body = "";
			this.isShowModal = false;
		},
		closeOverlay : function (e) {
			if (e.target.id == "overlay") {
				if (this.kbn == "POST_COMPLETE" || this.kbn == "DELETE_COMPLETE") {
					//更新時は無効
					return;
				}

				document.getElementById('body').className = "";
				this.kbn = "";
				this.title = "";
				this.body = "";

				this.isShowModal = false;
			}
		},
	}
}

createApp(App)
.component("popup-window", popupWindow)
.component('slide-up-down', SlideUpDown)
.use(VueGtag, {
	config: {
		id: 'G-BEZT3PLNS3', 
		router
	}
})
.use(router, axios)
.mount('#app')
