<template>
  <div class="Top2" style="min-height:100vh">
    <div :style="'height:' + this.$root.pageHeight + 'px'" />
    <div class="mainArea bgBlack2">
      <div style="width:100%; text-align:left">
        <div style="text-align:center">
          <span class="list_label_day">コメント投稿量グラフ</span>
          <div style="height:0px" />
          <div class="titleBar" style="width:40%; margin:auto;" />
          <div class="sp_line_ss" />
          <div class="bgBlack3" style="padding:10px; border-radius:8px">
            <div class="fcWhite1" style="text-align:left; margin-top:10px; font-size:14px; width:100%; margin:auto">
              「好き嫌い.com」というサイトで、ストグラの主催者しょぼすけ氏のスレッドの時間ごとの投稿数を割り出して、グラフ表にしました。   話題があった時間の書き込みを見に行ったり、参加者のアーカイブを視聴する際などにご活用ください。<br>
              ・赤い行：ストグラ運営時刻<br>
              ・青い行：ストグラ非運営時刻<br>
              ・紫のアイコン：「好き嫌い.com」の該当ページにジャンプ
            </div>
          </div>
        </div>
        <div class="sp_line_s" />
        
        <div v-for="(data, index1) in this.sukiDataList" :key="index1">
          <div class="list_label_day" style="display:flex; flex-wrap:wrap; justify-content: flex-start;">
            <div style="text-align:left">
              <img :src="this.$root.IMG_PATH + 'suki/icon_suki_' + openIcon(index1) + '.png'" style="width:30px; cursor:pointer; padding-bottom:7px" v-on:click="open(index1)">&nbsp;
              {{data.date}}（<span :class="youbiClass(data.youbi)">{{data.youbi}}</span>）17時
            </div>
            <div style="text-align:center">&nbsp;～&nbsp;</div>
            <div style="text-align:right; padding-right:15px">
              {{data.dateEd}}（<span :class="youbiClass(data.youbiEd)">{{data.youbiEd}}</span>）17時
            </div>
          </div>
          <div class="sp_line_s" />
          <div style="display:flex; text-align:center; gap:2%">
            <div class="bgBlack3" style="padding:10px; width:32%; border-radius:8px">
              <div class="fcWhite2">総コメント数</div>
              <div class="fcWhite1" style="margin-top:10px; font-size:20px">{{data.sum}}</div>
            </div>
            <div class="bgBlack3" style="padding:10px; width:32%; border-radius:8px">
              <div class="fcWhite2">最多時刻</div>
              <div class="fcWhite1" style="margin-top:10px; font-size:20px">{{data.maxT}}</div>
            </div>
            <div class="bgBlack3" style="padding:10px; width:32%; border-radius:8px">
              <div class="fcWhite2">最多投稿数</div>
              <div class="fcWhite1" style="margin-top:10px; font-size:20px">{{data.max}}</div>
            </div>
          </div>
          <div class="sp_line_s" />
          <slide-up-down :active="data.active" :duration="250">
            <div v-for="(data2, index2) in this.sukiDataList[index1].list" :key="index2">
              <div style="text-align:center">
                <div v-for="(data3, index3) in this.sukiDataList[index1].list[index2].list" :key="index3">
                  <div class="list_label_row">
                    <div style="width: 70px">{{data3.time}}</div>
                    <div style="width: 50px" :class="getCol(0, data.date, data2.hours)">{{data3.count}}&nbsp;</div>
                    <div style="width: calc(100% - 160px);" class="list_bar_back">
                      <div class="list_bar_percent" :class="getCol(1, data.date, data2.hours)" :style="'width: ' + getGra(data.max, data3.count) + '%'" />
                    </div>
                    <div style="width: 40px; text-align:center">
                      <img :src="this.$root.IMG_PATH + 'suki/icon_suki_jump2.png'" style="width:27px; cursor:pointer" v-on:click="jump(data3.no)">
                    </div>
                  </div>
                  <div class="sp_line_ss" />
                </div>
              </div>
              <div class="list_label_hour">
                {{data2.hours}}時
              </div>
              <div class="sp_line_s" />
            </div>
          </slide-up-down>
          <div class="sp_line_s" />
        </div>
        <div style="text-align:center">
          <button type="button" v-show="lastDateStr != '2025/2/26'" class="topBtn btn-purple" v-on:click="pushMore()">さらに読み込む</button>
        </div>
        <div class="sp_line_l" />
      </div>
    </div>
  </div>
  <button class="topBtn btn-green" style="position:fixed; right:20px; bottom:20px" v-on:click="pushTop">最新へ</button>
</template>

<script>
import axios from 'axios'
import { event } from 'vue-gtag-next'

export default {
  props: {
  },
  components: {
  },
  data() {
    return {
      sukiDataList : [],

      lastDate : null,

      curDate : null,
      curHourStr : "",
      curNo : 0,

      descriptionActive : false,
    }
  },
  created : function () {
  },
  computed : {
    lastDateStr : function () {
      if (this.lastDate == null) {
        return "";
      } else {
        return this.lastDate.getFullYear() + "/" + (this.lastDate.getMonth() + 1) + "/" + this.lastDate.getDate();
      }
    },
    youbiClass : function () {
      return (youbi) => {
        if (youbi == "土") {
          return "youbiDo";
        } else if (youbi == "日") {
          return "youbiNi";
        } else {
          return "";
        }
      }
    },
    openIcon : function () {
      return (index) => {
        if (this.sukiDataList[index].active == true) {
          return "minus";
        } else {
          return "plus";
        }
      }
    },
    getCol : function () {
      return (kbn, dateStr, hours) => {
        let date = new Date(dateStr);
        // console.log(date.getDay() + " " + hours + " " + date);
        if (date.getDay() == 1 || (4 < hours && hours < 17)) {
          if (kbn == 0) {
            return "fcBlue1";
          } else {
            return "list_bar_blue";
          }
        } else {
          if (kbn == 0) {
            return "fcRed1";
          } else {
            return "list_bar_red";
          }
        }
      }
    },
    getGra : function () {
      return (max, count) => {
        if (count == "-") {
          return 0;
        }
        return count / max * 100.0;
      }
    }
  },
  methods : {
    pushTop : function () {
      window.scrollTo(0, 0);
    },
    open : function(index) {
      let flg = this.sukiDataList[index].active;
      this.sukiDataList[index].active = !flg;
    },
    jump : function(no) {
      window.open("https://suki-kira.com/people/result/%E3%81%97%E3%82%87%E3%81%BC%E3%81%99%E3%81%91/?nxc=" + (no + 1));
    },
    pushMore : function () {
      this.getList();
    },
    initAction : function () {
      //現在日付を取得
      let nowDate = new Date();
      if (nowDate.getHours() < 17) {
        //前の日のデータとして扱うので1日減らす
        nowDate.setDate(nowDate.getDate() - 1);
      }
      nowDate.setHours(17);
      nowDate.setMinutes(0);
      nowDate.setSeconds(0);
      nowDate.setMilliseconds(0);

      this.lastDate = nowDate;
      console.log(this.lastDate);

      this.$root.pageAction("HOME");
      this.getList();
    },
    getList : function () {
      this.$root.showLoad(); 

      let params = new URLSearchParams();
      let str = this.lastDate.getFullYear() + "/" + String(this.lastDate.getMonth() + 1).padStart(2, '0') + "/" + String(this.lastDate.getDate()).padStart(2, '0');
      params.append("date", str);
      console.log(str);

      axios.post(this.$root.SERVER_PATH + "getList", params)
      .then((response) => {
        let resData = response.data;
        console.log(resData);
        let sukiList = resData["sukiList"];
        // console.log(sukiList);
        //データを調整 補完
        for (let i = 0; i < sukiList.length; i++) {
          const element = sukiList[i];
          let dateStr = element.created_at;
          let date = new Date(dateStr);
          element.created_at = date.getFullYear() + "/" + (date.getMonth() + 1) + "/" + date.getDate() + " " + date.getHours() + ":" + date.getMinutes();
        }

        //その日と前の日のデータを追加
        let date1 = this.lastDate;
        let date2 = new Date(this.lastDate.getTime());
        date2.setDate(date2.getDate() - 1);
        let date3 = new Date(this.lastDate.getTime());
        date3.setDate(date3.getDate() + 1);

        let youbi = this.lastDate.getDay();
        let youbiStr1 = "";
        let youbiStr2 = "";
        let youbiStr3 = "";
        if (youbi == 0) {youbiStr1 = "日"; youbiStr2 = "土"; youbiStr3 = "月" }
        else if (youbi == 1) {youbiStr1 = "月"; youbiStr2 = "日"; youbiStr3 = "火" }
        else if (youbi == 2) {youbiStr1 = "火"; youbiStr2 = "月"; youbiStr3 = "水" }
        else if (youbi == 3) {youbiStr1 = "水"; youbiStr2 = "火"; youbiStr3 = "木" }
        else if (youbi == 4) {youbiStr1 = "木"; youbiStr2 = "水"; youbiStr3 = "金" }
        else if (youbi == 5) {youbiStr1 = "金"; youbiStr2 = "木"; youbiStr3 = "土" }
        else if (youbi == 6) {youbiStr1 = "土"; youbiStr2 = "金"; youbiStr3 = "日" }
        
        let dateStr1 = date1.getFullYear() + "/" + String(date1.getMonth() + 1).padStart(2, '0') + "/" + String(date1.getDate()).padStart(2, '0');
        let dateStr2 = date2.getFullYear() + "/" + String(date2.getMonth() + 1).padStart(2, '0') + "/" + String(date2.getDate()).padStart(2, '0');
        let dateStr3 = date3.getFullYear() + "/" + String(date3.getMonth() + 1).padStart(2, '0') + "/" + String(date3.getDate()).padStart(2, '0');
        this.sukiDataList.push({
          "youbi" : youbiStr1,
          "date" : dateStr1,
          "youbiEd" : youbiStr3,
          "dateEd" : dateStr3,
          "list" : [],
          "active" : true,
          });
        this.sukiDataList.push({
          "youbi" : youbiStr2,
          "date" : dateStr2,
          "youbiEd" : youbiStr1,
          "dateEd" : dateStr1,
          "list" : [],
          "active" : true,
          });

        //日付ごとのデータに分割
        let maxCount = 0;
        let maxTime = "";
        let sum = 0;
        let maeFlg = false;
        for (let i = 0; i < sukiList.length; i++) {
          const element = sukiList[i];

          //日付
          let date = new Date(element.created_at);
          
          let targetDateList = null;
          if (date >= this.lastDate) {
            //日付１
            targetDateList = this.sukiDataList[this.sukiDataList.length - 2];
          } else {
            //日付２
            targetDateList = this.sukiDataList[this.sukiDataList.length - 1];
            if (!maeFlg) {
              //日付の変わり目
              maeFlg = true;
              sum = 0;
              maxCount = 0;
            }
          }

          //時間データ
          let hourStr = date.getHours();
          let hourDispStr = date1.getFullYear() + "/" + String(date1.getMonth() + 1).padStart(2, '0') + "/" + String(date1.getDate()).padStart(2, '0') + " " + date.getHours();
          let targetHourDataList = [];
          if (targetDateList.list.length != 0 && this.curHourStr == hourStr) {
            //時間が同じ場合
            targetHourDataList = targetDateList.list[targetDateList.list.length - 1].list;
          } else {
            targetDateList.list.push({
              "hours" : hourStr,
              "dispHours" : hourDispStr,
              "list" : targetHourDataList,
              "active" : false,
            })
          }

          //日時
          let timeStr = date.getHours() + ":" + String(date.getMinutes()).padStart(2, '0');

          //前回との差分
          let sabun = 0;
          if (this.curDate != null) {
            sabun = (this.curDate - date) / (1000 * 60);
          }

          //No
          let no = element.no;

          //カウント
          let count = "-";
          if (this.curNo != 0 && sabun == 2) {
            count = this.curNo - no;
            sum += count;
            targetDateList.sum = sum;
          }

          this.curDate = date;
          this.curNo = no;
          this.curHourStr = hourStr;

          if (count != "-" && maxCount < count) {
            maxCount = count;
            maxTime = timeStr;
            targetDateList.max = maxCount;
            targetDateList.maxT = maxTime;
          }

          targetHourDataList.push({
            "time" : timeStr,
            "no" : no,
            "count" : count,
          });
        }
        this.$root.closePopup();
        this.lastDate.setDate(this.lastDate.getDate() - 2);
        console.log(this.sukiDataList);
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  watch : {
  }
}
</script>

<style>
  .youbiDo {
    color: #00B4F4;
  }
  
  .youbiNi {
    color: #F4004E;
  }
</style>