  <div class="Top2" style="min-height:100vh">
    <div :style="'height:' + this.$root.pageHeight + 'px'" />
    <div class="mainArea bgBlack2">
      <div style="width:100%; text-align:left">
        <div style="text-align:center">
          <span class="list_label_day">グッズ関連</span>
          <div style="height:0px" />
          <div class="titleBar" style="width:40%; margin:auto;" />
          <div class="sp_line_ss" />
          <div class="bgBlack3" style="padding:10px; border-radius:8px">
            <div class="fcWhite1" style="text-align:left; margin-top:10px; font-size:14px; width:100%; margin:auto">
              メルカリに出品されているストグラ関連のグッズをピックアップしてみました。 推しのグッズや欲しかった商品がゲットできるかも！？<br>
              ※タップするとメルカリのページにジャンプします。
            </div>
          </div>
        </div>
        <div class="sp_line_s" />

        <div style="text-align:center">
          <button type="button" style="padding:6px 20px" class="topBtn btn-outline btn-outline-yellow" v-on:click="pushMeru()">
            <span class="font_3xl">もっとグッズを見に行く</span>
          </button>
          <div class="sp_line_sss" />
          <span class="font_xl">タップするとメルカリのページにジャンプします</span>
        </div>

        <div class="sp_line_s" />
        <div style="display:flex; flex-wrap:wrap; justify-content:center; gap:10px; padding:10px; width:100%">
          <div style="" v-for="(data2, index2) in this.goodsAllList" :key="index2">
            <div style="background:#fff;width:170px;height:265px;box-sizing:border-box;border:1px solid #ccc;display:flex;flex-direction:column;justify-content:flex-start;align-items:center;">
              <div style="line-height:0;">
                <img src="https://static.jp.mercari.com/assets/img/common/jp/logo_horizontal.png" width="105">
              </div>
              <a :href="'https://jp.mercari.com/item/' + data2.url + '?afid=0750724495'" style="width:150px;height:150px;background:#eee;" target="_blank">
                <img :src="'https://ambassador-system.mercari.com/v1/i?id=' + data2.url + '&svc=m'" style="line-height:0;width:150px;height:150px;object-fit:contain;"/>
              </a>
              <div style="padding:12px 0;width:100%;text-align:center;">
                <a :href="'https://jp.mercari.com/item/' + data2.url + '?afid=0750724495'" style="width:100px;height:32px;background-color:#E32B36;border-radius:4px;line-height:14px;text-align:center;color:#fff;font-weight:bold;border:0;font-size:12px;display:inline-flex;justify-content:center;align-items:center;" target="_blank">
                  今すぐ購入
                </a>
              </div>
            </div>
          </div>
        </div>
        <div class="sp_line_l" />
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  props: {
  },
  components: {
  },
  data() {
    return {
      goodsAllList : [],
    }
  },
  created : function () {
  },
  computed : {
    openIcon : function () {
      return (index) => {
        if (this.dispDataList[index].active == true) {
          return "minus";
        } else {
          return "plus";
        }
      }
    },
  },
  methods : {
    pushMeru : function () {
      window.open("https://jp.mercari.com/search?afid=0750724495&page_token=v1%3A1&search_condition_id=1cx0zHHN0HTEcax3jgrnjg4jjgrDjg6k");
    },
    pushTop : function () {
      window.scrollTo(0, 0);
    },
    open : function(index) {
      let flg = this.dispDataList[index].active;
      this.dispDataList[index].active = !flg;
      // console.log(flg);
    },
    initAction : function () {
      this.$root.pageAction("GOODS");
      this.getList(true);
    },
    getList : function () {
      this.$root.showLoad(); 

      let params = new URLSearchParams();
      axios.post(this.$root.SERVER_PATH + "getGoodsList", params)
      .then((response) => {
        let resData = response.data;
        // console.log(resData);
        let goodsList = resData["goodsList"];

        this.goodsAllList = goodsList;
        for (let i = 0; i < this.goodsAllList.length; i++) {
          const element = goodsList[i];
          element.url = element.url.replace("https://jp.mercari.com/item/", "");
        }
        this.$root.closePopup();
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  watch : {
  }
}
</script>

