<template>
  <div id="headder" class="headerBase bgBlack1" style="text-align:left;">
    <div class="sp_line_ss" />
    <div style="display:flex; justify-content: space-between;">
      <img :src="this.$root.IMG_PATH + 'suki/logo_suki.png'" style="width:40%; max-width:250px; cursor:pointer" v-on:click="pushBtn('HOME')">
      <div>
        <button type="button" style="padding:3px 9px" class="topBtn btn-outline btn-outline-red" v-on:click="pushBtn('HOME')">Home</button>&nbsp;&nbsp;
        <button type="button" style="padding:3px 9px" class="topBtn btn-outline btn-outline-yellow" v-on:click="pushBtn('GOODS')">グッズ関連</button>&nbsp;
      </div>
    </div>
    <div class="sp_line_ss" />
    <div class="bar" style="border-color:white" />
  </div>
  <div class="bodyBase">
    <span id="scTop" />
    <div id="baseParent" style="text-align:center;">
      <router-view/>
    </div>
  </div>
  <div :class="[pageClass['bc']]" style="color:white">
    <div class="sp_line_s" />
    <span class="pointer" v-on:click="pushBtn('TWITTER')">公式X（旧twitter）</span> &nbsp;/&nbsp;
    <span class="pointer" v-on:click="pushBtn('TERMS')">利用規約</span> &nbsp;/&nbsp;
    <span class="pointer" v-on:click="pushBtn('PRIVACY')">プライバシーポリシー</span> &nbsp;/&nbsp;
    <div class="sp_line_s" />
    運営者：ストグラフ運営事務局
    <div class="sp_line_sss" />
    お問い合わせ先:m_info@sutograph.net<br>
    <div class="sp_line_s" />
  </div>

  <div id="overlay" v-show="this.isPopup">
  </div>
</template>

<script>
import axios from 'axios'

// 受験生・学生のための情報交換用ウェブサイトを開設しました！
// 掲示板形式で、勉強の報告や受験状況の情報共有などを通じてみんなでがんばりましょう🏫
// https://manapiyo.com

// #まなぴよ #受験 #受験2023 #受験生
  export default {
    components : {
    },
    data() {
      return {
        API_SUCCESS : 200,
        API_FAILED_LOGIN : 99,
        API_FAILED_AUTH : 100,
        API_FAILED_PARAM : 300,
        API_FAILED_NODATA : 400,
        API_FAILED_DUPLICATE : 401,
        API_FAILED_MISMATCH : 402,
        API_FAILED_PRIVATE : 500,
        API_FAILED_FILE : 600,
        API_FAILED_EXEPTION : 900,

        IMG_PATH : "/app/img/", 
        ICON_PATH : "/storage/icon/",
        CARD_PATH : "/storage/card/",
        
        // BASE_URL : "http://localhost:8001/",
        // SERVER_PATH : "http://localhost:8001/api/",
        // ROOT_PATH : "/",
        // DOMAIN_PATH : "localhost",
        // CSRF_URL : "http://localhost:8001/sanctum/csrf-cookie",
        
        // BASE_URL : "https://studytimer.tarolab.jp/",
        // SERVER_PATH : "https://studytimer.tarolab.jp/api/", 
        // ROOT_PATH : "/",
        // DOMAIN_PATH : "studytimer.tarolab.jp",
        // CSRF_URL : "https://manapiyo.com/sanctum/csrf-cookie",
        
        // BASE_URL : "https://sutograph.net/",
        // SERVER_PATH : "https://sutograph.net/api/",
        // ROOT_PATH : "/",
        // DOMAIN_PATH : "sutograph.net",
        // CSRF_URL : "https://sutograph.net/sanctum/csrf-cookie",

        pageName : "",
        reloadValue : "", //画像の更新用,

        isPopup: false,
        popupKbn: "",
        popupTitle: "",
        popupBody: "",
        popupBodyClass : "popupBodyLeft",

        abList : null,
        abIndexList : [
          {kbn : "GROUP_1", url : ""},
          {kbn : "GROUP_1", url : ""},
          {kbn : "GROUP_1", url : ""},
          {kbn : "GROUP_1", url : ""},
        ],
      
        baseParentWidth : 1000,
        pageClass : {},

        pageHeight : 0,
      }
    },
    computed : {
      popupClass : function () {
        if (this.listRowData == null) {
          return "graPopup bocPopup";
        } else {
          return "popupRow"
        }
      },
      mainBc : function () {
        if (this.pageName == "TOP" || this.pageName == "ILLUST") {
          return "bcYellow";
        } else {
          return "bcWhite";
        }
      },
      logoIndex : function () {
        let retIndex = 0;
        switch (this.pageName) {
          case "REPORT": retIndex = 2; break;
          case "EXAMINEE": retIndex = 3; break;
          case "GUCHI": retIndex = 6; break;
          case "SOUDAN": retIndex = 4; break;
          case "RESPONSE": retIndex = 4; break;
          case "NANDEMO": retIndex = 5; break;
          default: retIndex = 1; break;
        }
        return retIndex;
      },
      timerStatusLabel : function () {
        if (this.status == "INIT") { return "未起動"; }
        else if (this.status == "START") { return "起動中"; }
        else if (this.status == "PAUSE") { return "一時停止中"; }
        else if (this.status == "STOP") { return "停止中"; }
        return "";
      },
      catlinkClass : function () {
        return (catName) => {
          if (catName == this.pageName) {
            return "";
          } else {
            return "link";
          }
        }
      },
      myDegreeBtnClass : function () {
        return (index) => {
          if (index == this.degree) {
            return "degreeBtnActive";
          } else {
            return "";
          }
        }
      },
      degreeBtnClass : function () {
        return (index) => {
          for (let i = 0; i < this.dispDegreeList.length; i++) {
            if (index == this.dispDegreeList[i]) {
              return "degreeBtnActive";
            }
          }
          return "";
        }
      },
      kenLabel : function () {
        return (index) => {
          return this.$root.allKenList[index];
        }
      },
      degreeLabel : function () {
        return (val) => {
          val = parseInt(val);
          if (val == 0) {
            return "指定なし";
          } else if (val == 1) {
            return "中学生";
          } else if (val == 2) {
            return "高校生";
          } else if (val == 3) {
            return "大学生";
          } else if (val == 4) {
            return "社会人";
          } else if (val == 5) {
            return "その他";
          } else if (val == 99) {
            return "運営";
          }
          return "";
        }
      },
      userIdLabel : function () {
        return (userId) => {
          if (userId == null || userId.length < 6) {
            return "";
          } else if (userId == "manapiyo") {
            return "(" + userId + ")";
          } else {
            return "(" + userId.substring(0, 5) + ")";
          }
        }
      },
      abc : function () {
        return this.cardBaseScale;
      }
    },
    created : function() {
      // localStorage.clear();
      //広告情報取得
      axios.get(this.$root.SERVER_PATH + 'getAbs')
      .then((response) => {
        // console.log(response.data);
        this.abList = response.data["abs"];
        // this.pageAction();
      });
    },
    mounted () {
      window.addEventListener('resize', this.calcHeight);
      window.onload = () => {
        this.calcHeight();
      };
    },
    methods : {
      calcHeight : function () {
        let height = document.getElementById("headder").offsetHeight;
        this.pageHeight = height * 1.2;
      },
      chDispDegree : function (index) {
        let val = this.dispDegreeList[index];
        if (val == 0) {
          val = 1;
        } else {
          val = 0;
        }
        this.dispDegreeList[index] = val;
      },
      retPageClass : function (kbn) {
        let name = kbn;
        name = "";
        // if (kbn == "HOME" || kbn == "" || kbn == "ILLUST" || kbn == "HOWTO" || kbn == "GUIDELINES"
        //  || kbn == "CONTACT" || kbn == "TERMS" || kbn == "PRIVACYPOLICY"){ name = "Main"}
        // else if (kbn == "REPORT"){ name = "Report"}
        // else if (kbn == "EXAMINEE"){ name = "Examinee"}
        // else if (kbn == "GUCHI"){ name = "Guchi"}
        // else if (kbn == "SOUDAN" || kbn == "RESPONSE"){ name = "Soudan"}
        // else if (kbn == "NANDEMO"){ name = "Nandemo"}

        return {
          name : name.toLowerCase(),
          fc: "fc" + name, 
          boc: "boc" + name, 
          bc: "bc" + name, 
          bc2: "bc" + name + "2", 
          db: "dotBar" + name,
          sya : "sya" + name,
          gra : "gra" + name,
        };
      },
      closeOverlay : function (e) {
        if (e.target.id == "overlay") {
          this.isShowListRow = false;
        }
      },
      pushCatBtn : function (name) {
        
        if (name == "HOME") {
          this.$router.push("/top2");
        } else if (name == "REPORT") {
          this.$router.push("/report");
        } else if (name == "GUCHI") {
          this.$router.push("/guchi");
        } else if (name == "SOUDAN") {
          this.$router.push("/soudan");
        } else if (name == "NANDEMO") {
          this.$router.push("/nandemo");
        } else if (name == "EXAMINEE") {
          this.$router.push("/examinee");
        } else if (name == "ILLUST") {
          this.$router.push("/illust");
        } else if (name == "HOWTO") {
          this.$router.push("/howto");
        }

        
      },
      calculateWindowWidth : function () {
        this.baseParentWidth = document.getElementById("baseParent").clientWidth;
      },
      chkInput : function (name, isReq, length, val) {
        if (isReq && val == "") {
          return name + "が未入力です"
        } else if (val.length > length) {
          return name + "は" + length + "文字以内で入力してください";
        }
        
        return "";
      },
      pushBtn : function (name) {
        if (name == "HOME") {
          if (this.pageName == "HOME") {
            location.reload();
          } else {
            this.$router.push("/");
          }
        } else if (name == "GOODS") {
          this.$router.push("/goods");
        }

        if (name == "TERMS") {
          this.$router.push("/terms");
        } else if (name == "PRIVACY") {
          this.$router.push("/privacypolicy");
        } else if (name == "TWITTER") {
          window.open("https://twitter.com/sutograph");
        } else if (name == "CONTACT") {
          this.$router.push("/contact");
        }
      },
      autoScroll : function(kbn) {
        if (kbn == "HOME") {
          document.getElementById("scTop").scrollIntoView({  
            behavior: 'smooth'
          });
        } else if (kbn == "EDIT") {
          document.getElementById("editParent").scrollIntoView({  
            block : 'start',
            behavior: 'smooth',
          });
        } else if (kbn == "LISTEND") {
          document.getElementById("listEnd").scrollIntoView({  
            behavior: 'smooth',
            block : 'center'
          });
        }
      },
      showLoad: function() {
        this.showPopup("LOAD", "", "");
      },
      showPopup: function(kbn, title, body) {
        document.getElementById('body').className = "modalBg";
        this.popupKbn = kbn;
        this.popupTitle = title;
        this.popupBody = body;
        this.isPopup = true;
      },
      pushPopupBg : function () {
        if (this.popupKbn == "TIPS") {
          document.getElementById('body').className = "";
          this.isPopup = false;
        }        
      },
      closePopup : function () {
        document.getElementById('body').className = "";
        this.isPopup = false;
      },
      pushHome : function () {
        if (this.pageName == "HOME") {
          location.reload();
        } else {
          this.$router.push("/");
        }
      },
      pageAction : function (pageName) {
        this.autoScroll("HOME");
        if (pageName != "") {
          this.pageName = pageName;
        }
        this.pageClass = this.retPageClass(this.pageName);
      },
      pushMenuClose : function () {
        this.isMenuOpen = false;
      },
    }
  }
</script>

<style>
#app {
  font-family: "Noto Sans JP", "Zen Maru Gothic","Hiragino Kaku Gothic Pro", Meiryo, "Yu Gothic", "Source Han Sans", "Source Han Sans JP", "Noto Sans JP", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  /* color: #2c3e50; */
}

.bm-menu {
  padding-top:30px !important;
}

.fill {
  vertical-align: -5px !important;
  font-variation-settings:
  'FILL' 1,
  'wght' 300,
  'GRAD' 0,
  'opsz' 24 !important;
}

.material-symbols-outlined {
  font-variation-settings:
  'FILL' 0,
  'wght' 400,
  'GRAD' 0,
  'opsz' 24
}

.popupRow {
  border:none !important;
}

</style>
